@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@600&display=swap);
@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 400;
  src: url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Medium.woff2) format('woff2'),
  url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Medium.woff) format('woff'),
  url(//fonts.gstatic.com/ea/notosanskr/v2/NotoSansKR-Medium.otf) format('opentype');
}

body {
  margin: 0;
  font-family: 'Titillium Web', 'Noto Sans KR', 'Roboto', 'Oxygen', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p, h1, h2, h3, h4, h5, h6 {
	margin: 0;
	padding: 0;
}

ul, ol {
	list-style: none;
	margin: 0;
	padding: 0;
}

.App {
	width: 100%;
}
.header {
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    color: #000000;
    z-index: 10;
}

.header__menu {
    display: flex;
    float: right;
    background-color: rgba(255, 255, 255, 0.3);
    list-style: none;
    padding: 8px 12px;
}

.header__menu li {
    padding: 8px 12px;
}

.header__menu li:hover {
    background-color: rgb(255, 255, 255, 0.8);
}
.background {
    position: fixed;
    top: 0;
    right: 0;
    z-index: -1;
    width: 100%;
    height: 100vh;
}

.back-img {
    width: 100%;
    height: 100vh;
    object-fit: cover;
}

.main {
    position: relative;
    height: 100vh;
    background: rgba(255, 255, 255, 0.5);
    line-height: 1em;
    font-size: 60px;
}

.main p {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    margin-top: -1em;
    margin-left: -50%;
    text-align: center;
}
.about {
    display: flex;
    flex-wrap: wrap;
    background: #ffffff;
    padding: 30px 50px 30px 50px;
}

.about__title {
    width: 100%;
    margin: 0 0 20px 0;
}

.about__title h1 {
    position: relative;
    width: 100px;
}

.about__title h1:after {
    position: absolute;
    top: 90%;
    left: 0;
    content: "";
    width: 85px;
    height: 2px;
    background-color: #7b7cd4;
}

.about__profile {
    width: 250px;
    height: 250px;
}

.about__content {
    padding: 0 0 0 20px;
}
.contact {
    padding: 30px 50px 30px 50px;
    background: #eeeeee;
}

.contact__title h1 {
    position: relative;
}

.contact__title h1:after {
    position: absolute;
    top: 90%;
    left: 0;
    content: "";
    width: 105px;
    height: 2px;
    background-color: #7b7cd4;
}

.contact__content {
    margin-top: 10px;
}
.skill {
    background-color: #fff;
    padding: 30px 50px 30px 50px;
}

.skill__title h1 {
    position: relative;
}

.skill__title h1:after {
    position: absolute;
    top: 90%;
    left: 0;
    content: "";
    width: 60px;
    height: 2px;
    background-color: #7b7cd4;
}

.skill__content {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
}

.skillName {
    width: 30%;
}

.skillList {
    list-style: circle;
    width: 70%;
    margin-bottom: 30px;
}
.project {
    background-color: #eeeeee;
    padding: 30px 50px 30px 50px;
}

.project__title h1 {
    position: relative;
}

.project__title h1:after {
    position: absolute;
    top: 90%;
    left: 0;
    content: "";
    width: 100px;
    height: 2px;
    background-color: #7b7cd4;
}

.footer {
    padding: 10px 0 10px 0;
    background-color: bisque;
    text-align: center;
}
