.project {
    background-color: #eeeeee;
    padding: 30px 50px 30px 50px;
}

.project__title h1 {
    position: relative;
}

.project__title h1:after {
    position: absolute;
    top: 90%;
    left: 0;
    content: "";
    width: 100px;
    height: 2px;
    background-color: #7b7cd4;
}